.App {
  text-align: center;
}

.App-wrapper {
  width: 1000px;
  margin: 50px auto;
  padding: 10px;
}

.App-title {
  text-align: center;
  color: white;
}

.top-nav {
  width: 80%;
  margin: 0 auto;
}

.btn-right {
  float: right;
}

.datalist {
  display: flex;
  flex-direction: row;
  border: 1px solid black;
  padding: 5px;
  margin: 10px auto;
  background-color: rgb(243, 32, 32);
  border-radius: 10px;
  justify-content: space-between;
  width: 80%;
}

.datalist:hover {
  width: 90%;
}

.button-list {
  align-self: center;
}

.data-text {
  text-transform: uppercase;
  font-weight: bold;
  color: rgb(29, 23, 23);
}

.btn {
  margin: 0 5px;
}

.close-btn:hover {
  cursor: pointer;
}

.isCompleted {
  background-color: rgb(3, 216, 74);
}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.modal-show {
  display: block;
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 10px;
  border: 1px solid #888;
  width: 50%; /* Could be more or less, depending on screen size */
}

.modal-header {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: bold;
}